* {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;

  color: #bbb;
}

p {
  font-size: 0.9em;
}

html,
body {
  margin: 0;
  padding: 0;
  background-color: #222;
}

#header {
  display: flex;
  align-content: center;
}

li {
  list-style-type: none;
}

#header li {
  display: inline;
}

#header a {
  text-decoration: none;
  display: inline-block;
  padding: 0.5em;
  margin: 1em;
}

a:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

#nav {
  margin: auto;
}

#my-links {
  display: inline;
}

#home {
  padding: 20px;
}

.card {
  height: 200px;
  width: 200px;
  border-radius: 10px;
  border-width: 1px;
  border-color: blue;
  border: solid;
  padding: 10px;
  margin: 20px;
}

.card-thumbnail {
  height: 120px;
  background-color: aqua;
  border-radius: 5px;
}

.card-container {
  max-width: 730px;
  display: flex;
  flex-wrap: wrap;
}



#card-filter {
  height: 200px;
  border: solid;
}

#content {
  max-width: 1070px;
  display: flex;
  margin: auto;
  border-top: 1px solid rgba(255, 255, 255, 0.3)
}

#intro {
  min-width: 300px;
  padding: 20px;
}

#intro-links {
  display: flex;
  justify-content: space-between;
  padding: 15px;
}

.thumbnail-text {
  padding: 5px;
  margin: 0px;
  color: #222;
}

.headline {
  margin: 0px;
  padding-top: 5px;
  padding-bottom: 5px;
}